* {
  margin: 0;
  padding: 0;
}
input {
  margin-top: 10px;
  width: 30%;
  border-radius: 4px;
  border: 1px solid #4e5766;
  box-shadow: 1px 0.5px #888888;
  font-size: 18px;
}
button {
  width: 60px;
  margin-left: 5px;
  font-size: 18px;
}
.mealFinder {
  background-color: #2d2013;
  text-align: center;
  min-height: 400vh;
}
.mealFinder .heading {
  padding-top: 20px;
  margin-bottom: 20px;
  color: white;
  font-weight: 700;
}

.showMeals {
  position: relative;
  width: fit-content;
  height: fit-content;
  margin: auto;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0px 0px 10px #2d2013;
  display: grid;
  justify-content: center;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  grid-template-rows: auto;
  grid-gap: 50px;
}
.meal {
  position: relative;
  width: 50%;
  height: auto;
  margin: auto;
  background-color: #f2f2f2;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  transition: opacity 0.2s ease-in;
}
.meal img {
  width: 100%;
  height: 100%;
  border-radius: 2px;
}

.dataNotfound {
  color: #f2f2f2;
  font-size: 20px;
}
.meal h2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.meal:hover h2 {
  z-index: 100;
  color: #f2f2f2;
  width: fit-content;
  height: fit-content;
}
.meal:hover {
  opacity: 0.6;
}
